import React from 'react';
import { Switch, Redirect, Route } from 'react-router-dom';

import {
//  SignUp as SignUpView,
  SignIn as SignInView,
  Main as MainView,
//  NotFound as NotFoundView
} from './views';

const Routes = () => {
  return (
    <Switch>
      <Redirect
        exact
        from="/"
        to="/main"
      />
      <Route
        component={SignInView}
        exact
        path="/sign-in"
      />
      <Route
        component={MainView}
        exact
        path="/main"
      />
      <Redirect to="/sign-in" />
    </Switch>
  );
};

export default Routes;
